.sideMenu{
    background: white;
    width: 20%;
    height: 93vh;
    /* border-right: 1px solid red; */
    box-shadow: 1px 1px 10px rgb(141, 140, 140);
    position: relative;
}
#positionAbs{
    position: fixed;
    bottom: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: white;
    height: 68px;
    width: 16%;
}
.lHover a:hover{
    background: red;
    color: red;
}
.font_size{
    font-size: 20px;
}
@media screen and (max-width:1200px) {
    .font_size{
        font-size: 15px;
    }
    .dis_flex{
        display:  flex;
        flex-direction: column;
        justify-content: center;
    }
    .dis_none{
        display: none;
    }
    .sideMenu{
        width: 50%;
    }
#positionAbs{
width: 32%;
}
}

